@tailwind base;
@tailwind components;
@tailwind utilities;

@import url('https://fonts.googleapis.com/css2?family=Inter:wght@300;400;500;600;700;800;900&display=swap');

@layer base {
    body {
        @apply font-[Inter];
    }
    li {
        @apply px-4;
        @apply cursor-pointer
    }
}

.bullet {
    fill: green;
    stroke: black;
    stroke-width: 2px;
}